/* styling */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  user-select: none;
}

.main {
  width: 70vw;
  min-height: 90vh;
  box-shadow: 0 0 14px 0;
  margin: 30px auto;
  border-radius: 5px;
  background-color: #343a40;
}

.box {
  padding: 10px;
  border-bottom: 2px solid #9e9b9b;
}
.title .qno {
  font-size: 1.4rem;
  font-weight: 800;
  font-family: "Courier New", Courier, monospace;
  background-color: #4d4d4d; 
  padding: 5px;
  border-radius: 50px;
  text-align: center;
  margin: auto 0;
  color: #fff;
}
.title h2 {
  font-size: 1.7rem;
  font-weight: 500;
  margin-left: 10px;
  color: #fcfcfc;
}
.box .title {
  display: flex;
}

.options {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 20px 25px;
  color: #ecebeb;
}

.options span {
  /* background: aliceblue; */
  border: 1px solid #5c5b5b !important;
  border-radius: 5px;
  padding: 10px !important;
  font-size: 18px;
  margin-bottom: 15px !important;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* icon style */

.iconstyle {
  color: white;
  background-color: darkgreen;
  border-radius: 12px;
}
.iconstyle1 {
  color: white;
  background-color: darkred;
  border-radius: 12px;
}

/* Counter and total */
.total {
  margin-bottom: 0 !important;
  width: 100%;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.total,
.head {
  padding: 10px;
  background-color: #ecebeb;
  text-align: center;
}

.total h3,
.head h3 {
  font-size: 1.7rem;
  font-weight: 800;
  color: #4c4c4c;
  line-height: 35px !important;
  font-family: "Courier New", Courier, monospace;
}

.total .span1 {
  font-size: 2rem;
  color: #84c5fe;
}
.total .span2 {
  font-size: 2rem;
  color: lightgreen;
}
.total .span3 {
  font-size: 2rem;
  color: tomato;
}

.head {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.timer {
  color: #004085;
  background: aliceblue;
  border: 1px solid #b8daff;
  height: 45px;
  padding: 0 8px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 145px;
}

.timer .time_left_txt {
  font-weight: 400;
  font-size: 17px;
  user-select: none;
}

.timer .timer_sec {
  font-size: 18px;
  font-weight: 500;
  height: 30px;
  width: 45px;
  color: #fcfcfc;
  border-radius: 5px;
  line-height: 30px;
  text-align: center;
  background: #343a40;
  border: 1px solid #343a40;
  user-select: none;
}
.timeout {
  text-align: center;
  margin-top: 5% !important;
  margin-bottom: 11% !important;
  font-size: 1.7rem;
  font-weight: 800;
  min-height: 45vh;
  height: auto;
  color: #61dafb;
  font-family: "Courier New", Courier, monospace;
}
.total h5{
  margin-top: 15px !important;
  font-size: 1.1rem;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.total h5 a{
  text-decoration: none !important;
  color: #8441f0;
}

/* Responsive */
@media screen and (max-width: 768px) {
  .head h3 {
    font-size: 1.8rem;
  }
  .total h3 {
    font-size: 1.2rem;
  }
  .total .span1,
  .total .span2,
  .total .span3 {
    font-size: 1.4rem;
  }
  .main {
    width: 100vw;
    margin: 0px auto;
  }
  .box {
    padding: 5px;
  }
  .title {
    margin-top: 5px;
  }
  .title .qno {
    font-size: 1rem;
  }
  .title h2 {
    font-size: 1.2rem;
  }
  .options {
    margin: 20px 7px;
  }
  .options span {
    border: 1px solid #f7f7f7;
    padding: 7px !important;
    font-size: 16px;
  }
  .total {
    bottom: 0 !important;
    position: absolute !important;
  }
  .timeout {
    margin-top: 30% !important;
  }
}
